import React, { useEffect, useState } from 'react';
import './App.css';
import {
    TextField,
    Grid,
    AppBar,
    Toolbar,
    Typography,
    IconButton,
    Button, Avatar
} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import imageToBase64 from 'image-to-base64/browser';
import OutlinedDiv from "./OutlinedDiv";


function App() {

    // Styles settings
    const useStyles = makeStyles((theme) => ({
       root: {
         flexGrow: 1,
       },
       menuButton: {
         marginRight: theme.spacing(2),
       },
       title: {
         flexGrow: 1,
       },
     }));
    const classes = useStyles();

    // Params
    const [picUrl, setPicUrl] = useState("");

    // Functions
    const convertImageToBase64 =(picUrl) => {
        console.log(picUrl)
        imageToBase64(picUrl) // Image URL
    .then(
        (response) => {
            console.log(response)
        }
    )
    .catch(
        (error) => {
            console.log(error)
        }
    )
    }


  return (

     <>
       <AppBar position="static">
        <Toolbar className='myToolbar'>
          <Typography variant="h6" className={classes.title}>
            Demo - Image Categorization Model
          </Typography>
        </Toolbar>
      </AppBar>

         <Grid container style={{height:"90vh", padding:50}}>

      <Grid item xs={12} style={{display:"flex",alignItems:"center", flexDirection:"column"}}>
      <TextField
          id="standard-read-only-input"
          label="Enter the picture's url here"
          onChange={(e)=> setPicUrl(e.target.value)}
          defaultValue=""
          value={picUrl}
          variant="outlined"
          style={{width:"100vh"}}
        />
        <br/>
        <br/>
        {picUrl !== "" ?
        <OutlinedDiv label="Your picture">
        <img
            src={picUrl}
        />
        </OutlinedDiv>
      : <></>}

      </Grid>

      <Grid item xs ={12} style={{display:"flex", alignItems:"center", flexDirection:'column'}}>
          {picUrl !== "" ?
              <div>
                  <br />
                  <br />
                  <Button style={{width:"20vh", height:"5vh"}} variant= "outlined" onClick={()=>convertImageToBase64(picUrl)}>Detect category</Button>
              </div>
      : <></>}
      <br/>

</Grid>
      </Grid>

     </>

  );
}


export default App;